<div class="w-auto h-[100vh] border-r-2"><div class="w-auto  p-2"><el-button class="w-full" :icon="Switch" @click="isCollapse = !isCollapse"></el-button></div><el-menu default-active="0" :collapse="isCollapse" background-color="#ffffff" @open="handleOpen" @close="handleClose" style="border-right: 0px;"><component v-for="(item, index) of menuData" :key="index" :is="item.child ? 'el-sub-menu' : 'el-menu-item'" :index="index + &quot;&quot;"><el-icon v-if="!item.child"><component v-if="!item.child" :is="item.icon"></component></el-icon><template v-if="!item.child" #title>{{item.name}}</template><template v-if="item.child" #title><el-icon><component :is="item.icon"></component></el-icon><span>{{item.name}}</span></template><el-menu-item-group v-if="item.child" v-for="(childItem, childIndex) of item.child"><el-menu-item :index="index + &quot;-&quot; + childIndex">{{childItem}}</el-menu-item></el-menu-item-group></component></el-menu></div>